import { defineStore } from "pinia";
import { settingsStore } from "@/store/settings";

interface WebAppData {
  web_app_data: {
    data_check_string: string | null;
    auth_date: string | null;
    hash: string | null;
  };
}

interface UserData {
  username: string;
  firstName: string;
  lastName: string;
  language: string;
}

export const telegramStore = defineStore("telegram", {
  state: () => ({
    webAppData: null as null | WebAppData,
    userData: null as null | UserData,
    referalCode: null as null | string,
  }),

  actions: {
    // Возвращает телеграмовские данные пользователя (язык, имя и т.д.), а если их нет - получает от Телеграма
    ensureUserData(): UserData {
      if (!this.userData) {
        try {
          this.userData = this.generateUserData();
        } catch (error) {
          console.error("Ошибка при получении телеграм данных пользователя:", error);
          throw new Error("Error when getting the user data from telegram")
        }
      }
      return this.userData;
    },

    // Получает от Телеграма данные пользователя (язык, имя и т.д.)
    generateUserData() {
      const telegram = (window as any).Telegram;
      if (typeof window !== "undefined" && telegram && telegram.WebApp) {
        telegram.WebApp.ready();

        telegram.WebApp.expand();
        telegram.WebApp.lockOrientation();
        telegram.WebApp.requestFullscreen();
        telegram.WebApp.disableVerticalSwipes()

        const startParam = telegram.WebApp.initDataUnsafe.start_param;
        if (this.referalCode === null && startParam) {
          try {
            this.checkReferalCode(startParam);
          } catch (error) {
            throw new Error("Ошибка при проверке реферального кода.")
          }
        }

        const user = telegram.WebApp.initDataUnsafe?.user;

        if (user) {
          return {
            username: user.username || "No username",
            firstName: user.first_name || "Unknown",
            lastName: user.last_name || "Unknown",
            language: user.language_code || "en",
          };
        } else {
          throw new Error("User в Telegram WebApp API не доступен.")
        }
      } else {
        throw new Error("Telegram WebApp API не доступен.")
      }
    },

    // Проверяет наличие валидного реферального кода в startParam и вызывает запрос о привязке на сервер
    checkReferalCode(startParam: string) {
      const match = startParam?.match(/ref_([a-zA-Z0-9]+)/);

      if (match && match[1]) {
        this.referalCode = match[1];

        if (this.referalCode)
            settingsStore().becomeReferal(this.referalCode);
      }
    },

    // TODO: ошибки выбрасывать
    // Возвращает валидационный объект web_app_data, а если его нет - получает от Телеграма
    ensureWebAppData(): WebAppData {
      if (!this.webAppData) {
          this.webAppData = this.generateWebAppData();
      }
      return this.webAppData;
    },

    // Получает от Телеграма (WebApp.initData) данные для валидационного объекта
    generateWebAppData(): WebAppData {
      const telegram = (window as any).Telegram;
      if (typeof window !== "undefined" && telegram && telegram.WebApp) {
        telegram.WebApp.ready();

        const user = telegram.WebApp.initDataUnsafe?.user;

        if (user) {
          const queryString = telegram.WebApp.initData;
          const params = new URLSearchParams(queryString);

          const authDate = params.get("auth_date");
          const hash = params.get("hash");

          params.delete("hash");

          const dataCheckString = Array.from(params.entries())
            .sort(([keyA], [keyB]) => keyA.localeCompare(keyB))
            .map(([key, value]) => `${key}=${value}`)
            .join("\n");

          return {
            web_app_data: {
              data_check_string: dataCheckString,
              auth_date: authDate,
              hash: hash,
            },
          };
        } else {
          throw new Error("User в Telegram WebApp API не доступен.");
        }
      } else {
        throw new Error("Telegram WebApp API не доступен.");
      }
    },
  },

  getters: {},
});
